import { Component, OnInit, ElementRef } from '@angular/core';
import * as smoothscroll from "smoothscroll-polyfill";
import { throttle } from 'lodash';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.css']
})
export class HeaderComponent implements OnInit {

  menus = [
    {
      name: 'Home',
      href: '#home',
      isActive: true,
    },
    {
      name: 'About',
      href: '#about',
      isActive: false,
    },
    {
      name: 'Work',
      href: '#work',
      isActive: false,
    },
    {
      name: 'Skills',
      href: '#skills',
      isActive: false,
    },
    {
      name: 'Contact',
      href: '#contact',
      isActive: false,
    },
  ];

  smoothScroll(e, param) {
    e.preventDefault();
    history.replaceState(undefined, undefined, param)
    document.querySelector(param).scrollIntoView({ behavior: 'smooth' });
    this.menus.forEach((e) => {
      if (e.href === param) {
        console.log(e.href, param);
        e.isActive = true;
      } else {
        e.isActive = false;
      }
    });
  }

  constructor(private el: ElementRef) {
    
  }

  

  ngOnInit() {
    
    smoothscroll.polyfill();
  }

  ngAfterViewInit() {
    const ele = this.el.nativeElement.querySelector('.main-header');
    
    const headerTransition = function () {
      if (window.pageYOffset > window.innerHeight - 1) {
        ele.classList.add('main-header--transitioned');
      } else {
        ele.classList.remove('main-header--transitioned');
      }
    };
    const throttledHeaderTransition = throttle(headerTransition, 250, { 'leading': true })
    window.addEventListener('scroll', throttledHeaderTransition);
  }

}



